.custom-field .indicator {
    position: absolute;
    top: 0px;
    left: 0;
    height: 20px;
    width: 20px;
    border: solid 1px #D1D3D4;
    background: #fff;
    border-radius: 4px;
    transition: 0.2s all ease-in-out;
}
.custom-field .indicator:after {
      content: '';
      position: absolute;
      display: none;
}
.custom-field input {
    position: absolute;
    z-index: -1;
    opacity: 0;
}
.custom-field input[type="checkbox"] ~ .indicator:after {
      left: 7px;
      top: 4px;
      width: 5px;
      height: 9px;
      border: solid #fff;
      border-width: 0 2px 2px 0;
      transform: rotate(45deg);
}
.custom-field input[type="radio"] ~ .indicator {
      border-radius: 50%;
}
.custom-field input[type="radio"] ~ .indicator:after {
        left: 6px;
        top: 6px;
        height: 10px;
        width: 10px;
        border-radius: 50%;
        background: #f78f1d;
}
.custom-field input:disabled ~ .indicator {
      background: #fff;
      border: solid 1px #D1D3D4;
      opacity: 0.6;
      pointer-events: none;
      cursor: not-allowed;
}
.custom-field input:disabled ~ span {
      cursor: not-allowed;
}
.custom-field input:checked ~ .indicator {
      border-color: #f78f1d;
      background: #f78f1d;
}
.custom-field input:checked ~ .indicator:after {
        display: block;
}
.custom-field:hover .indicator {
    border-color: #f78f1d;
    background: #f78f1d;
}
