/* Slider */
/* Icons */
@font-face {
  font-family: "slick";
  src: url("../assets/fonts/slick.eot");
  src: url("../assets/fonts/slick.eot?#iefix") format("embedded-opentype"), url("../assets/fonts/slick.woff") format("woff"), url("../assets/fonts/slick.ttf") format("truetype"), url("../assets/fonts/slick.svg#slick") format("svg");
  font-style: normal;
  font-weight: normal;
}

/* Arrows */
.slick-prev,
.slick-next {
  background: transparent;
  border: none;
  color: transparent;
  cursor: pointer;
  display: block;
  font-size: 0px;
  height: 20px;
  line-height: 0px;
  margin-top: -10px;
  outline: none;
  padding: 0;
  position: absolute;
  top: 50%;
  width: 20px;
}
.slick-prev:hover, .slick-prev:focus,
  .slick-next:hover,
  .slick-next:focus {
    background: transparent;
    color: transparent;
    outline: none;
}
.slick-prev:hover:before, .slick-prev:focus:before,
    .slick-next:hover:before,
    .slick-next:focus:before {
      opacity: 1;
}
.slick-prev.slick-disabled:before,
  .slick-next.slick-disabled:before {
    opacity: 1;
}
.slick-prev, .slick-next {
  color: #D1D3D4;
}
.slick-prev:before, .slick-next:before {
    font-family: "slick";
    font-size: 20px;
    line-height: 1;
    opacity: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.slick-prev:hover, .slick-next:hover {
    color: #f7901a;
}
.slick-prev:active, .slick-prev:focus, .slick-next:active, .slick-next:focus {
    color: #D1D3D4;
}
.slick-prev:active:hover, .slick-prev:focus:hover, .slick-next:active:hover, .slick-next:focus:hover {
      color: #f7901a;
}
.slick-prev {
  left: -25px;
}
[dir="rtl"] .slick-prev {
    left: auto;
    right: -25px;
}
.slick-prev:before {
    content: "";
}
[dir="rtl"] .slick-prev:before {
      content: "";
}
.slick-next {
  right: -25px;
}
[dir="rtl"] .slick-next {
    left: -25px;
    right: auto;
}
.slick-next:before {
    content: "";
}
[dir="rtl"] .slick-next:before {
      content: "";
}

/* Dots */
.slick-dots {
  bottom: -45px;
  display: block;
  padding: 0;
  list-style: none;
  margin-left: 0;
  position: initial;
  text-align: center;
  width: auto;
}
.slick-dots li {
    color: transparent;
    cursor: pointer;
    display: inline-block;
    height: 20px;
    margin: 0 3px;
    padding: 0;
    position: relative;
    width: 20px;
}
.slick-dots li button {
      color: transparent;
      cursor: pointer;
      background: transparent;
      border: 0;
      display: block;
      font-size: 0px;
      height: 20px;
      line-height: 0px;
      padding: 5px;
      outline: none;
      width: 20px;
}
.slick-dots li button:hover, .slick-dots li button:focus {
        outline: none;
}
.slick-dots li button:hover:before, .slick-dots li button:focus:before {
          opacity: 1;
}
.slick-dots li button:before {
        color: transparent;
        content: "";
        font-family: "slick";
        font-size: 6px;
        height: 20px;
        left: 0;
        line-height: 20px;
        opacity: 1;
        position: absolute;
        text-align: center;
        top: 0;
        width: 9px;
        height: 9px;
        background-color: white;
        border: 1px solid #323232;
        border-radius: 50%;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before {
      background-color: #323232;
      border: 1px solid white;
      opacity: 1;
}
